<template>
    <div class="login-box">
        <div>
            <div class="top">
                <i style="font-size: 2.5rem;font-weight: bold;color: #e9e9e9;letter-spacing: 2px">面试刷题平台-管理员</i>
            </div>
            <div class="middle">
                <div style="color: white" class="title">
                    管理员平台登录
                </div>
                <Input v-model="admin" placeholder="请输入管理员账号" clearable style="width: 100%;line-height: 4.5rem;" />
                <Input v-model="password" placeholder="请输入账号密码" clearable type="password" style="width: 100%;line-height: 4.5rem;" />
                <Button @click="adminLogin" style="margin-top: 1.5rem;" type="primary" long>登录管理平台</Button>
            </div>
        </div>
    </div>
</template>

<script>
    import config from './../../../config'
    import {adminLogin} from "@/api";

    export default {
        name: "login",
        data(){
            return{
                admin: '',
                password: ''
            }
        },
        methods:{
            async adminLogin(){
                let {admin, password} = this;
                if(!admin){
                    return this.$Message['warning']({
                        background: true,
                        content: '请填写账号'
                    });
                }
                if(!password){
                    return this.$Message['warning']({
                        background: true,
                        content: '请输入密码'
                    });
                }
                let data = await adminLogin({
                    admin, password
                });
                if(data.code === 200){
                    config.setCache('adminId', data.result.adminInfo.id);
                    this.$store.dispatch('addAdminInfo', {adminInfo: data.result.adminInfo});
                    this.$router.replace('/admin');
                    this.$Message['success']({
                        background: true,
                        content: data.data
                    });
                }else{
                    this.$Message['error']({
                        background: true,
                        content: data.data
                    });
                }
            }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
    .login-box{
        position fixed
        width: 100%
        height: 100%
        left: 0
        top: 0
        z-index 999
        background-color: #b8e5f8
        >div{
            width: 100%
            height: 100%
            background url("./../../../common/images/adminLogin.gif") no-repeat
            background-size: cover;
            >.middle{
                width: 50rem
                margin: 0 auto
                padding: 2rem 2rem 3rem 2rem
                background-color: rgba(255,255,255,.5)
                box-shadow: 0 .1rem .3rem 0 rgba(0,0,0,.1);
                border-radius .5rem
                overflow: hidden;
                >.title{
                    width: 100%
                    height: 3rem
                    display flex
                    align-items center
                    justify-content center
                    font-size: 1.8rem
                    font-weight: 600
                    margin-bottom: 2rem
                }
            }
            >.top{
                width: 100%
                height: 20rem
                display flex
                align-items center
                justify-content center
            }
        }
    }
</style>
